<template>
    <ItemDetail :previewHeight="420" v-if="data.ready">
        <template v-slot:background>
            <Img
                v-if="data.item?.profileImage"
                :preview="data.item?.profileImage?.crop"
                :original="data.item?.profileImage?.original"
                :alt="data.item.name"
                :skeleton="true"
                :checktone="true"
                @tone="data.tone = $event"
            />
        </template>
        <template v-slot:preview>
            <div class="name"
                :class="{ invert: !sameColors }"
                v-if="data.item?.name && data.tone"
                @click="openArtist"
            >{{ data.item.name }}</div>


            <div class="buttons" v-if="data.tone">
                <IconButton :name="t('player.shuffle')"
                    :class="{ invert: !sameColors }"
                    class="tertiary size-xl dirty" icon="random"
                />

                <AudioToggle
                    :state="state"
                    :light="true"
                    class="size-l secondary dirty"
                    :class="{ invert: data.tone !== 'dark' }"
                    @play="play"
                    @pause="pause"
                />
            </div>
        </template>
        <template v-slot:toolbar>
            <template v-if="data.tone">
                <IconButton
                    icon="unlike"
                    class="tertiary size-xl"
                    :class="{ invert: others.theme !== 'dark' }"
                    @click="like"
                />

                <IconButton
                    icon="tg-stars"
                    class="tertiary size-xl"
                    @click="like"
                />

                <IconButton
                    icon="apps"
                    class="tertiary size-l"
                    :class="{ invert: others.theme !== 'dark' }"
                    @click="openArtistKebab"
                />
            </template>
        </template>
        <template v-slot:detail>
            <Releases
                v-if="data.item?.id"
                :take="1"
                :skeletonCount="1"
                :params="{
                    contributors: data.item.id,
                    releaseDate_lte: moment().toISOString(),
                    releaseDate_gte: moment().subtract(1, 'year').toISOString()
                }"
                :loadMore="false"
                :light="false"
                :sort="{
                    code: 'releaseDate',
                    direction: 'desc'
                }"
                title="blocks.releases.new-release"
                @change="openRelease"
            />


            <Tracks
                v-if="data.item?.id"
                type="row"
                :take="12"
                :params="{
                    contributors: data.item.id,
                    state: 'PUBLISHED'
                }"
                :sort="{
                    code: 'ratingTotal',
                    direction: 'desc'
                }"
                :options="{
                    type: 'artist',
                    id: data.item.id
                }"
                :loadMore="false"
                :buttons="[{
                    name: 'blocks.tracks.all',
                    page: 'artist-tracks',
                    params: {
                        code: data.item.code
                    }
                }]"
                title="blocks.tracks.popular-tracks"
            />
            

            <Releases
                v-if="data.item?.id"
                type="row"
                :take="8"
                :skeletonCount="8"
                :params="{
                    contributors: data.item.id,
                    state: 'PUBLISHED'
                }"
                :sort="{
                    code: 'releaseDate',
                    direction: 'desc'
                }"
                :loadMore="false"
                :buttons="[{
                    name: 'blocks.releases.all',
                    page: 'artist-releases',
                    params: {
                        code: data.item.code
                    }
                }]"
                title="blocks.releases.latest-releases"
                handler="release"
            />


            <ArtistPreviewCard :artist="data.item" v-if="data.item?.id" />
        </template>
    </ItemDetail>

    <Ready :page="`artist-${props.code}`" :init="init" />
</template>

<script setup>
    import { onMounted, onBeforeUnmount, reactive, computed } from 'vue';
    import api from 'ðŸ”—/api';
    import stores from '@/stores';
    import Ready from 'ðŸ”—/components/ui/Ready.vue';
    import Img from 'ðŸ”—/components/ui/Img.vue';
    import Button from 'ðŸ”—/components/ui/Button.vue';
    import ItemDetail from 'ðŸ”—/components/ui/ItemDetail.vue';
    import ArtistPreviewCard from '@/components/ui/ArtistPreviewCard.vue';
    import IconButton from 'ðŸ”—/components/ui/IconButton.vue';
    import AudioToggle from 'ðŸ”—/components/ui/AudioToggle.vue';

    import Releases from 'ðŸ”—/components/blocks/Releases.vue';
    import Tracks from 'ðŸ”—/components/blocks/Tracks.vue';
    import { useI18n } from 'vue-i18n';
    import moment from 'moment';
    import { message } from 'ðŸ”—/utils/utils';

    const { t } = useI18n();

    const locale = stores.locale();
    const nav = stores.nav();
    const tg = stores.tg();
    const modals = stores.modals();
    const others = stores.others();
    const player = stores.player();

    const props = defineProps({
        code: { type: String }
    });

    const data = reactive({
        ready: false,
        item: null,
        tone: null
    });

    const state = computed(() => {
        if (!data.item?.id || !player.panes.default || player.panes.default._queue?.id !== data.item?.id) {
            return;
        }

        return player.panes.default._state;
    });

    const sameColors = computed(() => {
        if (data.tone === null) {
            return null;
        }

        return (others.theme !== 'dark' && data.tone !== 'dark') || (others.theme === 'dark' && data.tone === 'dark');
    })

    const pause = async () => {
        player.artist.pause(data.item.id);
    }

    const play = async () => {
        player.artist.play(data.item.id);
    }

    const like = (e) => {
        let button = e?.target?.closest('button');

        if (button) {
            button.classList.remove('dunk');
            setTimeout(() => {
                button.classList.add('dunk');
            }, 10);
        }

        message.info(t('common.soon'))
    }

    const init = async () => {
        data.item = await api.artists().getByCode(props.code);
        data.ready = true;
    }

    const openRelease = (item) => {
        if (!item?.id) {
            return;
        }

        nav.open('release', { id: item?.id });
    }

    const openArtist = () => {
        modals.open('artist', { quietClose: true, data: {
            code: props.code,
            item: data.item
        } })
    }

    const openArtistKebab = () => {
        modals.open('artist-kebab', { quietClose: true, data: {
            code: props.code,
            item: data.item
        } })
    }

    onMounted(async () => {
        tg.showBackButton();
        tg.showSettingsButton(openArtistKebab);
    });

    onBeforeUnmount(() => {
        tg.hideSettingsButton(openArtistKebab);
        tg.hideBackButton();
    });
</script>


<style lang="scss" scoped>
    .name {
        font: var(--font-h4);
        color: var(--dark-500);
        font-weight: bold;
        text-align: center;
        -webkit-text-stroke: .5px var(--white-90);
        text-stroke: .5px var(--white-90);

        &.invert {
            color: var(--white-500);
            -webkit-text-stroke: .5px var(--dark-90);
            text-stroke: .5px var(--dark-90);
            // &:before {
            //     background: var(--dark-90);
            //     box-shadow: 0px 0px 37px 22px var(--dark-90);
            // }
        }
    }

    .buttons {
        min-height: 38px;
        display: flex;
        flex-direction: row;
        gap: 8px;
        align-items: center;
        justify-content: center;

        button {
            // min-width: 132px;
        }
    }
</style>